footer {
  padding-top: 1em;
  color: $neutral;
  background-color: lighten($color: $secondary, $amount: 20);
  .footer-title {
    width: 70%;
    margin: 1em auto 2em;
    text-align: center;
    h2 {
      font-size: 30px;
      margin-bottom: 2px;
      color: $primary;
    }
    span {
      color: $primary;
    }
  }

  .footer-1 {
    display: grid;
    grid-template-columns: 60% 40%;
    justify-content: center;
    padding: 0 5%;
    margin: 1em auto;

    form {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      row-gap: 2em;
      input,
      textarea {
        padding: 1em;
        border: none;
        outline: none;
      }
      input:focus,
      textarea:focus {
        background-color: darken($color: $neutral, $amount: 10);
      }
      .name {
        width: 95%;
        justify-self: flex-start;

        input {
          width: 100%;
          height: 40px;
        }
      }
      .email {
        width: 95%;
        justify-self: flex-end;

        input {
          width: 100%;
          height: 40px;
        }
      }
      .textarea {
        grid-column: 1/4;
        width: 100%;
        textarea {
          width: 100%;
          height: 100px;
        }
      }
      button {
        padding: 1em 0;
        width: 150px;
        text-align: center;
        background: $primary;
        border: none;
        outline: none;
        color: $blue;
        font-size: 1em;
        transition: all 0.5s ease-out;
        &:hover {
          background-color: $secondary;
        }
      }
    }
    & > div {
      width: 100%;
      margin-left: 1em;
      & > div {
        margin-bottom: 1em;
        p {
          font-weight: bolder;
          color: $primary;
          font-size: 20px;
        }
      }
    }
  }

  .footer-2 {
    width: 100%;
    margin: 0 auto;
    position: relative;
    padding: 1em 0;
    background-color: $secondary;
    text-align: center;
    span {
      color: $primary;
    }

    .social-media {
      width: 100%;
      padding-bottom: 10px;
      display: flex;
      justify-content: center;

      a {
        color: $neutral;
        background: lighten($color: $secondary, $amount: 10);
        padding: 0.4em 0.3em;
        text-align: center;
        width: 45px;
        margin: 0 1em;
      }
      a:hover {
        background: $primary;
      }
    }
  }
}

@media only screen and (max-width: 700px) {
  footer {
    .footer-title {
      width: 100%;
      padding: 1px 5%;
    }
    .footer-1 {
      form {
        margin: 1em auto;
        row-gap: 0.8em;
      }
      grid-template-columns: 100%;
      & > div {
        margin-top: 2em;
        margin-left: 0;
      }
    }
  }
}
